@import "../../../colors.scss";

.title {
  font-size: 26px;
  font-weight: 400;
  margin-bottom: 14px;
}

.subtitle {
  font-size: 1em;
  margin-bottom: 14px;
}

p {
  margin-bottom: 24px;
  color: $text;
}

.list {
  list-style: none;
  margin-bottom: 24px;
  li {
    margin-bottom: 10px;
    &::before {
      content: "\2014";
      margin-right: 13px;
    }
  }
}

h3 {
  font-size: 18px;
}

.medium {
  font-weight: 500;
}

.blue {
  display: block;
  color: $light-blue;
  font-weight: 500;
  margin-bottom: 18px;
  a {
    color: $light-blue;
    font-weight: 700;
  }
}
