.card {
  width: 100%;
}
.card .img {
  display: block;
  width: 100%;
}
.card .about {
  padding: 25px;
  padding-bottom: 50px;
  border: 1px solid #eeeeee;
  border-bottom-left-radius: 6px;
  border-bottom-right-radius: 6px;
  border-top: none;
  letter-spacing: -3%;
}
.card .about .title {
  font-size: 1em;
  margin-bottom: 15px;
}
.card .about .text {
  font-size: 0.88em;
  margin-bottom: 10px;
  color: #242424;
}
.card .about .link {
  float: right;
  color: #5581F1;
  text-decoration: none;
  font-weight: 600;
}
.card .about .link:hover {
  text-decoration: underline;
}