.post {
  margin-bottom: 25px;
  cursor: pointer;
}
.post .img {
  width: 100%;
  height: 215px;
  border-radius: 4px;
  display: block;
  position: relative;
}
.post .img img {
  width: 100%;
}
.post .img::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 101.5%;
  border-radius: 4px;
  background: rgba(85, 129, 241, 0.1);
}
.post .stub {
  width: 100%;
  height: 200px;
  border-radius: 4px;
  display: block;
  position: relative;
  border-radius: 4px;
  background: #e6e6e6;
}
.post .txtContainer {
  background-color: #FEFEFE;
  z-index: -1;
  position: relative;
  left: -30px;
  top: -20px;
  border: 1px solid #e6e6e6;
  border-radius: 4px;
  padding: 0 35px;
  padding-top: 35px;
  padding-bottom: 11px;
  transition: all 0.5s ease;
}
.post .txtContainer .title {
  font-size: 1.32em;
  font-weight: 500;
  margin-bottom: 10px;
  color: #242424;
}
.post .txtContainer .text {
  color: #242424;
  line-height: 22px;
  transition: height 0.5s ease;
  height: 5.3rem;
  overflow: hidden;
  text-overflow: ellipsis;
  margin-bottom: 15px;
}
.post .txtContainer .date {
  color: #C4C4C4;
}
.post .txtContainer .category {
  display: none;
}
.post .txtContainer::after {
  content: attr(category);
  position: absolute;
  transition: all 0.5s ease;
  z-index: -999;
  left: 102%;
  top: 15%;
  height: 80%;
  display: flex;
  align-items: center;
  width: 1px;
  color: #141414;
  font-size: 0.96em;
}
.post:hover .txtContainer {
  transition: all 0.7s ease;
  padding-top: 20px;
  top: -5px;
  left: 0;
}
.post:hover .text {
  transition: height 0.7s ease;
  height: 11rem;
}

@media screen and (max-width: 1400px) {
  .post .txtContainer {
    left: -15px;
    top: -10px;
    padding: 25px;
  }
}
@media screen and (max-width: 1250px) {
  .post .img {
    height: 105px;
    margin-bottom: 10px;
  }
  .post .img img {
    height: 100%;
  }
  .post .img::before {
    height: 105px;
  }
  .post .stub {
    height: 105px;
    margin-bottom: 10px;
  }
  .post .txtContainer {
    left: 0;
    top: 0;
    border: none;
    padding: 0;
    padding-bottom: 11px;
  }
  .post .txtContainer .text {
    padding-bottom: 3px;
    border-bottom: 1px solid #e6e6e6;
    margin-bottom: 7px;
  }
  .post .txtContainer .category {
    display: inline-block;
    float: right;
    margin-right: 5px;
    letter-spacing: -1.8px;
    color: #6d92f3;
  }
  .post .txtContainer::after {
    content: none;
  }
  .post:hover .txtContainer {
    padding-top: 0;
    top: 0;
    left: 0;
  }
  .post .textActive {
    height: 21rem !important;
  }
}