.banner {
  width: 100%;
  padding: 78px 0;
  background-color: #00b1eb;
  color: #FEFEFE;
  font-weight: bold;
  font-size: 2.32em;
  text-align: center;
  border-radius: 4px;
  margin-bottom: 30px;
}

@media screen and (max-width: 1250px) {
  .banner {
    padding: 48px 0;
    margin-bottom: 22px;
  }
}