@import "../../colors.scss";

.title {
  margin-bottom: 20px;
}

.subtitle {
  font-size: 1.29em;
  font-weight: 500;
  margin-bottom: 18px;
}

.aboutCollege {
  margin-bottom: 18px;
  color: $text;
  line-height: 21px;
}

.personsAndDocuments {
  display: flex;
  margin-bottom: 18px;
  .subtitle {
    margin-bottom: 12px;
  }
  .persons {
    border-right: 1px solid $border;
    padding-right: 32px;
    margin-right: 32px;
    a {
      display: block;
      margin-bottom: 12px;
      text-decoration: none;
      color: $black;
      font-size: 1.05em;
      &:hover {
        text-decoration: underline;
      }
    }
  }
  .documentsLink {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 28px;
    a {
      display: block;
      margin-bottom: 12px;
      text-decoration: none;
      color: $black;
      font-size: 1.05em;
      &:hover {
        text-decoration: underline;
      }
    }
  }
}

.map {
  margin-bottom: 10px;
  width: 100%;
  height: 300px;
}

.infoContainer {
  display: flex;
  justify-content: space-between;
  span {
    display: block;
  }
  .phone {
    text-align: right;
  }
}

@media screen and (max-width: 600px) {
  .personsAndDocuments {
    display: flex;
    flex-direction: column;
    .subtitle {
      margin-bottom: 12px;
    }
    .persons {
      border-right: 0;
      padding-right: 0;
      margin-right: 0;
    }
    .documentsLink {
      grid-template-columns: repeat(1, 1fr);
    }
  }

  .infoContainer {
    align-items: flex-start;
    flex-direction: column;
  }
}
