@import "../../../colors.scss";

.title {
  font-size: 1.52em;
  font-weight: 400;
  margin-bottom: 14px;
}

.subtitle {
  font-size: 1em;
  margin-bottom: 14px;
}

p {
  margin-bottom: 24px;
  color: $text;
}

ul {
  list-style: none;
  margin-bottom: 24px;
}

li {
  margin-bottom: 10px;
  &::before {
    content: "\2014";
    margin-right: 13px;
  }
}

.titleText {
  line-height: 23px;
  .blue {
    display: block;
    color: $light-blue;
    font-weight: 500;
    a {
      color: $light-blue;
      font-weight: 700;
    }
  }
}
