@charset "UTF-8";
.title {
  font-size: 26px;
  font-weight: 400;
  margin-bottom: 14px;
}

.subtitle {
  font-size: 1em;
  margin-bottom: 14px;
}

p {
  margin-bottom: 24px;
  color: #242424;
}

.list {
  list-style: none;
  margin-bottom: 24px;
}
.list li {
  margin-bottom: 10px;
}
.list li::before {
  content: "—";
  margin-right: 13px;
}

h3 {
  font-size: 18px;
}

.medium {
  font-weight: 500;
}

.blue {
  display: block;
  color: #00C0FF;
  font-weight: 500;
  margin-bottom: 18px;
}
.blue a {
  color: #00C0FF;
  font-weight: 700;
}