.title {
  margin-bottom: 20px;
}

.subtitle {
  font-size: 1.29em;
  font-weight: 500;
  margin-bottom: 18px;
}

.aboutCollege {
  margin-bottom: 18px;
  color: #242424;
  line-height: 21px;
}

.personsAndDocuments {
  display: flex;
  margin-bottom: 18px;
}
.personsAndDocuments .subtitle {
  margin-bottom: 12px;
}
.personsAndDocuments .persons {
  border-right: 1px solid #e6e6e6;
  padding-right: 32px;
  margin-right: 32px;
}
.personsAndDocuments .persons a {
  display: block;
  margin-bottom: 12px;
  text-decoration: none;
  color: #141414;
  font-size: 1.05em;
}
.personsAndDocuments .persons a:hover {
  text-decoration: underline;
}
.personsAndDocuments .documentsLink {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 28px;
}
.personsAndDocuments .documentsLink a {
  display: block;
  margin-bottom: 12px;
  text-decoration: none;
  color: #141414;
  font-size: 1.05em;
}
.personsAndDocuments .documentsLink a:hover {
  text-decoration: underline;
}

.map {
  margin-bottom: 10px;
  width: 100%;
  height: 300px;
}

.infoContainer {
  display: flex;
  justify-content: space-between;
}
.infoContainer span {
  display: block;
}
.infoContainer .phone {
  text-align: right;
}

@media screen and (max-width: 600px) {
  .personsAndDocuments {
    display: flex;
    flex-direction: column;
  }
  .personsAndDocuments .subtitle {
    margin-bottom: 12px;
  }
  .personsAndDocuments .persons {
    border-right: 0;
    padding-right: 0;
    margin-right: 0;
  }
  .personsAndDocuments .documentsLink {
    grid-template-columns: repeat(1, 1fr);
  }

  .infoContainer {
    align-items: flex-start;
    flex-direction: column;
  }
}