.title {
  margin-bottom: 20px;
}

.linkBtns {
  display: grid;
  grid-template-columns: repeat(2, auto);
  column-gap: 35px;
  row-gap: 18px;
  text-align: center;
  margin-bottom: 30px;
}

.about {
  display: grid;
  grid-template-columns: repeat(2, auto);
  column-gap: 35px;
  row-gap: 18px;
}

@media screen and (max-width: 1250px) {
  .linkBtns {
    grid-template-columns: repeat(1, auto);
    column-gap: 0;
  }
  .about {
    grid-template-columns: repeat(1, auto);
    column-gap: 0;
  }
}
