@import "../../colors.scss";

.card {
  width: 100%;
  .img {
    display: block;
    width: 100%;
  }
  .about {
    padding: 25px;
    padding-bottom: 50px;
    border: 1px solid #eeeeee;
    border-bottom-left-radius: 6px;
    border-bottom-right-radius: 6px;
    border-top: none;
    letter-spacing: -3%;
    .title {
      font-size: 1em;
      margin-bottom: 15px;
    }
    .text {
      font-size: 0.88em;
      margin-bottom: 10px;
      color: $text;
    }
    .link {
      float: right;
      color: $dark-blue;
      text-decoration: none;
      font-weight: 600;
      &:hover {
        text-decoration: underline;
      }
    }
  }
}
