.accordion_list {
  list-style: none;
  cursor: pointer;
  margin: 10px 0;
  padding: 0;
  max-width: 100%;
  overflow: hidden;
}
.accordion_list .accordion_list_item::before {
  content: none;
}

.accordion_item__opened .accordion_item__icon {
  transform: rotate(180deg);
}
.accordion_item__opened .accordion_item__inner {
  max-height: 50%;
  transition-timing-function: cubic-bezier(0.895, 0.03, 0.685, 0.22);
  transition-duration: 0.5s;
  transition-property: max-height;
}
.accordion_item__opened .accordion_item__content {
  opacity: 1;
  transform: translateY(0);
  transition-delay: 0.2s;
  transition-timing-function: ease-in-out;
  transition-duration: 0.2s;
  transition-property: opacity, transform;
}
.accordion_item__line {
  padding: 0.4rem 0rem;
  padding-bottom: 0.2rem;
  display: flex;
  align-items: center;
  z-index: 2;
  position: relative;
}
.accordion_item__title {
  margin: 0;
  font-weight: 300;
  width: 100%;
}
.accordion_item__icon {
  padding: 4px;
  width: 30px;
  height: 30px;
  transition: transform 0.3s ease-in-out;
  background-size: contain;
  background-repeat: no-repeat;
  background-image: url(data:image/png;base64,);
  opacity: 0.6;
}
.accordion_item__inner {
  max-height: 0;
  overflow: hidden;
  text-transform: cubic-bezier(0.95, 0.05, 0.795, 0.035);
  transition-duration: 0.18s;
  transition-property: max-height;
  z-index: 1;
  position: relative;
}
.accordion_item__content {
  opacity: 0;
  transform: translateY(-1rem);
  transition-timing-function: linear, ease;
  transition-duration: 0.2s;
  transition-property: opacity, transform;
  transition-delay: 0.3s;
}

@media screen and (max-width: 1200px) {
  .accordion_list {
    list-style: none;
    cursor: pointer;
    margin: 10px 0;
    padding: 0;
    max-width: 100%;
    overflow: hidden;
  }
  .accordion_list .accordion_item__title {
    font-size: 1em;
  }
}