@import "../../colors.scss";

.post {
  margin-bottom: 25px;
  cursor: pointer;
  .img {
    width: 100%;
    height: 215px;
    border-radius: 4px;
    display: block;
    position: relative;
    img {
      width: 100%;
    }
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 101.5%;
      border-radius: 4px;
      background: rgba($dark-blue, 0.1);
    }
  }
  .stub {
    width: 100%;
    height: 200px;
    border-radius: 4px;
    display: block;
    position: relative;
    border-radius: 4px;
    background: $border;
  }
  .txtContainer {
    background-color: $white;
    z-index: -1;
    position: relative;
    left: -30px;
    top: -20px;
    border: 1px solid $border;
    border-radius: 4px;
    padding: 0 35px;
    padding-top: 35px;
    padding-bottom: 11px;
    transition: all 0.5s ease;
    .title {
      font-size: 1.32em;
      font-weight: 500;
      margin-bottom: 10px;
      color: $text;
    }
    .text {
      color: $text;
      line-height: 22px;
      transition: height 0.5s ease;
      height: 5.3rem;
      overflow: hidden;
      text-overflow: ellipsis;
      margin-bottom: 15px;
    }
    .date {
      color: $gray;
    }
    .category {
      display: none;
    }
    &::after {
      content: attr(category);
      position: absolute;
      transition: all 0.5s ease;
      z-index: -999;
      left: 102%;
      top: 15%;
      height: 80%;
      display: flex;
      align-items: center;
      width: 1px;
      color: $black;
      font-size: 0.96em;
    }
  }

  &:hover .txtContainer {
    transition: all 0.7s ease;
    padding-top: 20px;
    top: -5px;
    left: 0;
  }

  &:hover .text {
    transition: height 0.7s ease;
    height: 11rem;
  }
}

// TODO: офнути ховер ефект для мобілки

@media screen and (max-width: 1400px) {
  .post {
    .txtContainer {
      left: -15px;
      top: -10px;
      padding: 25px;
    }
  }
}

@media screen and (max-width: 1250px) {
  .post {
    .img {
      height: 105px;
      margin-bottom: 10px;
      img {
        height: 100%;
      }
      &::before {
        height: 105px;
      }
    }
    .stub {
      height: 105px;
      margin-bottom: 10px;
    }
    .txtContainer {
      left: 0;
      top: 0;
      border: none;
      padding: 0;
      padding-bottom: 11px;
      .text {
        padding-bottom: 3px;
        border-bottom: 1px solid $border;
        margin-bottom: 7px;
      }
      .category {
        display: inline-block;
        float: right;
        margin-right: 5px;
        letter-spacing: -1.8px;
        color: lighten($dark-blue, 5);
      }
      &::after {
        content: none;
      }
    }
    &:hover .txtContainer {
      padding-top: 0;
      top: 0;
      left: 0;
    }

    .textActive {
      height: 21rem !important;
    }
  }
}
