@charset "UTF-8";
.title {
  font-size: 1.52em;
  font-weight: 400;
  margin-bottom: 14px;
}

.subtitle {
  font-size: 1em;
  margin-bottom: 14px;
}

p {
  margin-bottom: 24px;
  color: #242424;
}

ul {
  list-style: none;
  margin-bottom: 24px;
}

li {
  margin-bottom: 10px;
}
li::before {
  content: "—";
  margin-right: 13px;
}

.titleText {
  line-height: 23px;
}
.titleText .blue {
  display: block;
  color: #00C0FF;
  font-weight: 500;
}
.titleText .blue a {
  color: #00C0FF;
  font-weight: 700;
}